@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;
@use "../../sass/helpers/mixins" as *;

.propierties {
  @include flex-gap(25px);
  padding: 20px;
  border: 2px dashed $blue-2-color;
  border-radius: 10px;
  color: $black-2-color;
  .controllers {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 50px;
    @include medias("mobile") {
      grid-template-columns: 1fr;
    }
  }
  .info {
    @include centerbyflex(null);
    gap: 10px 50px;
    flex-wrap: wrap;
    border-top: 2px dashed $blue-2-color;
    padding-top: 20px;
    font-size: 1rem;
    li.page {
      @include centerbyflex("vertical");
      gap: 10px;
      .page-label {
        font: {
          size: 1.1rem;
          weight: bold;
        }
      }
      .page-num {
        color: $green-3-color;
        font-size: 1.3rem;
      }
    }
    li.saved-verse-btn {
      button {
        display: block;
        box-shadow: $blue-3-6-shadow;
        background-color: $blue-3-color;
        border: none;
        border-radius: 5px;
        outline: none;
        padding: 10px 15px;
        font-size: 1.2rem;
        color: white;
        cursor: pointer;
        transition: 0.5s;
        &:hover {
          background-color: $blue-4-color;
          color: $yellow-color;
          box-shadow: $blue-3-8-shadow;
        }
      }
    }
  }
}
