@use "../../../sass/variables/colors" as *;
@use "../../../sass/variables/others" as *;
@use "../../../sass/helpers/mixins" as *;

.basmalah {
  display: block;
}

.verse-text {
  cursor: pointer;
  transition: 0.5s;
  display: inline;
  scroll-margin-top: 100px;

  &:hover,
  &.active {
    color: $blue-2-color;
  }
  .verse-icon {
    position: relative;
    font-size: 1.5625rem;
    font-family: $main-font;
    // vertical-align: middle;

    display: inline-block;
    &::after {
      content: attr(data-value);
      position: absolute;
      left: 50%;
      top: 50%;
      translate: -50% calc(-50% + 2px);
      font-size: 13px;
      line-height: 1;
    }
  }
}
