@use "../../../sass/variables/others" as *;
@use "../../../sass/variables/colors" as *;
@use "../../../sass/helpers/mixins" as *;

.sorah-name {
  position: relative;
  font-family: $quran-name-font;
  &:not(:first-child) {
    margin-top: 20px;
  }
  img {
    display: block;
  }
  h2 {
    position: absolute;
    left: 50%;
    top: 50%;
    translate: -50% -50%;
    @include medias("mobile") {
      font-size: 5vw;
    }
  }
}
