@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;
@use "../../sass/helpers/mixins" as *;

header {
  @include position(
    $blue-4-color,
    absolute,
    100%,
    $nav-height,
    $top: 0,
    $left: 0
  );
  color: white;
  z-index: 200000;
  box-shadow: $black-shadow;
  @include medias("mobile") {
    height: auto;
  }
  .container {
    @include centerbyflex("vertical");
    justify-content: space-between;
    height: 100%;
    padding-block: 10px;
    @include medias("mobile") {
      flex-direction: column;
      width: fit-content;
    }
    .logo {
      @include centerbyflex("vertical");
      gap: 15px;
      @include oneValue("h1", font-size, 1.1rem);
      @include oneValue("img", width, 50px);
      @include medias("mobile") {
        flex-direction: column;
        gap: 5px;
        text-align: center;
      }
    }
    nav {
      display: flex;
      justify-content: flex-end;
      gap: 25px;
      flex: 1;
      font-size: 1rem;
      text-align: center;
      @include medias("mobile") {
        justify-content: space-evenly;
        gap: 0;
        width: 100%;
      }
      a {
        @include centerbyflex(null);
        height: 100%;
        transition: 0.5s;
        &:hover {
          color: $yellow-color;
          scale: 1.2;
        }
      }
    }
  }
}
