@use "../../sass/helpers/mixins" as *;
@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;

.pagination {
  margin-bottom: 30px;
  .pagi-body {
    @include centerbyflex(null);
    gap: 10px;
    button {
      padding: 15px 30px;
      border: 1px solid $blue-3-color;
      border-radius: 10px;
      outline: none;
      transition: 0.5s;
      cursor: pointer;
      font-size: 1.125rem;
      color: $blue-3-color;
      @include medias("mobile") {
        padding: 10px 20px;
      }

      &:disabled {
        background-color: grey;
        @include one-value-more-properties($black-2-color, border-color, color);
        opacity: 0.5;
        cursor: default;
      }

      &:not(:disabled):hover {
        background-color: $blue-3-color;
        color: white;
      }
    }
    .pagi-info {
      text-align: center;
      font-size: 1.25rem;
      @include oneValue(".number", color, $blue-2-color);
    }
  }
  .inputs {
    position: relative;
    @include centerbyflex(null);
    flex-direction: column;
    .input-field {
      position: relative;
      width: fit-content;
      input {
        display: block;
        max-width: 200px;
        padding: {
          block: 10px;
          left: 20px;
        }
        border: 1px solid $blue-3-color;
        border-radius: 5px;
        outline: none;
        text-align: center;
        color: $blue-3-color;
        @include oneValue("&.error", border-color, $red-1-color);
      }
      .go {
        @include position(
          null,
          absolute,
          null,
          calc(100% - 2px),
          $top: 1px,
          $left: 1px
        );
        @include centerbyflex(null);
        padding: 5px 5px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        color: $blue-3-color;
        @include oneValue("&:disabled", color, #999);
      }
    }

    .errorMsg {
      display: block;
      font-size: 1rem;
      text-align: center;
      color: $red-1-color;
    }
  }
}
